.homeCategories{width:100%;padding:30px 20px}.homeCategories__inner{width:100%;margin:0 auto}.homeCategories__title{font-size:16px;color:#032c41;font-weight:700;margin-bottom:20px}.homeCategories__content{display:grid;grid-template-columns:repeat(2,1fr);gap:40px 30px;align-items:center;justify-content:center}@media (min-width:320px){.homeCategories__title{margin-bottom:20px}}@media (min-width:480px){.homeCategories__content{grid-template-columns:repeat(3,1fr)}}@media (min-width:600px){.homeCategories{padding:30px 50px}.homeCategories__title{font-size:20px}.homeCategories__content{grid-template-columns:repeat(3,1fr);gap:58px 50px}}@media (min-width:800px){.homeCategories__content{grid-template-columns:repeat(4,1fr)}}@media (min-width:1000px){.homeCategories__content{grid-template-columns:repeat(5,1fr)}}@media (min-width:1080px){.homeCategories__inner{width:1000px}.homeCategories__content{display:grid;grid-template-columns:repeat(6,1fr);gap:58px 50px}}@media (min-width:1280px){.homeCategories{max-width:100%}.homeCategories__inner{width:1200px}}